
#staff .teamName {
  text-align: center;
}
#staff .teamMember {
  margin: 0 auto;
  max-width: 680px;
  overflow-wrap: break-word;
  text-align: center;
  margin-bottom: 1.2rem;
}
.fourtitle {
  position: relative;
  margin-bottom: 4rem;
}
.fourtitle:before {
  content: '';
  display: inline-block;
  background-image: url("../assets/fourTitle.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 20rem;
  max-width: 90vw;
  height: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-left: -2.5rem;
}
.reversetitle:before {
  -webkit-transform: scaleX(-1) translate(50%, -50%);
          transform: scaleX(-1) translate(50%, -50%);
  -webkit-filter: FlipH;
          filter: FlipH;
  margin-left: 2.5rem;
}


