
#about:before {
  content: '';
  display: block;
  background-image: url("../assets/2017SC1.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 15rem;
  height: 30rem;
  margin-left: -15rem;
  margin-top: 8rem;
  position: absolute;
  z-index: 1;
}
#about .subtitle {
  text-align: center;
}
#about .subtitle:before {
  content: '';
  display: inline-block;
  background-image: url("../assets/red-car.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 1.6rem;
  margin-left: -3.5rem;
  margin-top: 0.2rem;
  position: absolute;
  z-index: 1;
}
#about .sixtitle {
  position: relative;
  margin-bottom: 4rem;
}
#about .sixtitle:before {
  content: '';
  display: inline-block;
  background-image: url("../assets/sixTitle.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 40rem;
  max-width: 90vw;
  height: 8rem;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
@media screen and (max-width: 600px) {
#about .sixtitle.longTitle:before {
    -webkit-transform: translate(-50%, -20%);
            transform: translate(-50%, -20%);
}
}
#about .reversetitle:before {
  -webkit-transform: scaleX(-1) translate(50%, -50%);
          transform: scaleX(-1) translate(50%, -50%);
  -webkit-filter: FlipH;
          filter: FlipH;
  margin-left: -2rem;
}


