
#registration {
  position: relative;
}
#registration .items {
  text-align: center;
  width: 300px;
}
#registration ul {
  list-style: none;
  line-height: 2rem;
  text-align: center;
  padding: 0;
}
#registration ol {
  line-height: 2rem;
}
#registration .button {
  font-size: 1.6rem;
  font-weight: 500;
  background-color: #566;
  color: #f8fcf6;
  margin: 1em;
  padding: 0.4em 0.8em;
  transition: all 0.3s;
  border: 1px solid transparent;
}
#registration .redcar {
  text-align: center;
}
#registration .redcar:before {
  content: '';
  vertical-align: -20%;
  display: inline-block;
  background-image: url("../assets/red-car.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 1.6rem;
  margin-right: 0.5rem;
  position: absolute;
  margin-left: -3.5rem;
  margin-top: 0.2rem;
  z-index: 1;
}
#registration .bluecar {
  text-align: center;
}
#registration .bluecar:before {
  content: '';
  vertical-align: -20%;
  display: inline-block;
  background-image: url("../assets/blue-car.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 1.6rem;
  margin-right: 0.5rem;
  position: absolute;
  margin-left: -3.5rem;
  margin-top: 0.2rem;
  z-index: 1;
}
#registration .twotitle {
  position: relative;
  margin-bottom: 4rem;
}
#registration .twotitle:before {
  content: '';
  display: inline-block;
  background-image: url("../assets/twoTitle.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 20rem;
  max-width: 90vw;
  height: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
#registration .reversetitle:before {
  -webkit-transform: scaleX(-1) translate(50%, -50%);
          transform: scaleX(-1) translate(50%, -50%);
  -webkit-filter: FlipH;
          filter: FlipH;
  margin-left: 2.5rem;
}


