
* {
  box-sizing: border-box;
}
body {
  color: #000;
  background-color: #afdcbe;
}
body.isShowFancyBox {
  overflow: hidden;
}
#app {
  font-family: 'Noto Sans', 'Source Hans Sans', 'Source Hans Sans TC', 'Noto Sans CJK TC', 'Noto Sans TC', 'PingFang TC', 'LiHei Pro', '微軟正黑體', 'Microsoft JhengHei', 'Avenir', Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  max-width: 100%;
}
.center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.title,
.subtitle {
  text-align: center;
  margin: 0 auto;
  font-size: 2.5rem;
}
.subtitle {
  font-size: 1.6rem;
}
.subtitle:before {
  content: '＃';
  text-indent: 0;
}
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.row.wrap {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.separate-around {
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.info {
  font-size: 1.1rem;
  text-indent: 0rem !important;
}
.container {
  z-index: 0;
  padding: 1em;
  margin: 0 auto;
  width: 60%;
}
@media screen and (max-width: 1440px) {
.container {
    width: 60%;
}
}
@media screen and (max-width: 1366px) {
.container {
    width: 65%;
}
}
@media screen and (max-width: 500px) {
.container {
    width: 90%;
}
}
@media screen and (max-width: 320px) {
.container {
    width: 95%;
}
}
.container .content {
  line-height: 2rem;
}
.container .content p {
  text-align: center;
}
.container .title {
  margin: 1em auto;
}
.container .subtitle {
  margin: 0.5em 0;
  padding: 0.5em;
}
.container a {
  text-decoration: none;
  color: #cc775f;
}


