
#exp .redcar {
  text-align: center;
}
#exp .redcar:before {
  content: '';
  vertical-align: -20%;
  display: inline-block;
  background-image: url("../assets/red-car.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 1.6rem;
  position: absolute;
  margin-left: -3.5rem;
  margin-top: 0.2rem;
  z-index: 1;
}
#exp .bluecar {
  text-align: center;
}
#exp .bluecar:before {
  content: '';
  vertical-align: -20%;
  display: inline-block;
  background-image: url("../assets/blue-car.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 1.6rem;
  position: absolute;
  margin-left: -3.5rem;
  margin-top: 0.2rem;
  z-index: 1;
}
#exp .fourtitle {
  position: relative;
  margin-bottom: 4rem;
}
#exp .fourtitle:before {
  content: '';
  display: inline-block;
  background-image: url("../assets/fourTitle.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 20rem;
  max-width: 90vw;
  height: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-left: -2.5rem;
}


