
#osc-logo {
  background-image: url("../assets/osc-logo.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}
@media screen and (min-width: 800px) {
#osc-logo {
    position: absolute;
    top: calc(3.5em + 15px + 1.5vh);
    left: 10vw;
    width: 25vw;
    min-width: 300px;
    max-width: 450px;
    height: calc(35vw / 2.23);
}
}
@media screen and (min-width: 768px) and (orientation: portrait) {
#osc-logo {
    position: absolute;
    top: calc(3.5em + 15px + 10vh);
    left: 5vw;
    width: 35vw;
    min-width: 300px;
    max-width: 600px;
    height: calc(35vw / 2.23);
}
}
@media screen and (max-width: 500px) and (orientation: portrait) {
#osc-logo {
    position: absolute;
    left: 40%;
    top: 30%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 250px;
    height: calc(250px / 2.23);
}
}
@media screen and (orientation: landscape) and (max-height: 500px) {
#osc-logo {
    position: absolute;
    left: 30%;
    top: 40%;
    width: 25vw;
    min-width: 300px;
    max-width: 400px;
    height: calc(25vw / 2.23);
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
}
#banner {
  min-height: 100vh;
}
#banner:before {
  background-image: url("../assets/city.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  height: 100vh;
  width: 100%;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
}
@media screen and (orientation: portrait) {
#banner:before {
    background-image: url("../assets/city-mobile.svg");
}
}
#banner h1,
#banner p,
#banner .button {
  -webkit-filter: drop-shadow(0 0 5px rgba(39,40,45,0.7));
          filter: drop-shadow(0 0 5px rgba(39,40,45,0.7));
}
#banner .button {
  min-height: calc(100vh * 0.075);
  min-width: calc(4em + 1.8rem * 4);
  font-size: 1.8rem;
  font-weight: 500;
  background-color: #7fb472;
  margin: 1.5em auto;
  padding: 0.2em 2em;
  box-shadow: 0px 0px 15px rgba(51,51,102,0.5);
  border-radius: 5px;
  position: absolute;
  top: calc(100vh * (0.85 - 0.0375));
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
@media screen and (max-width: 600px) {
#banner .button {
    padding: 0.2em 1.5em;
}
}
#banner .button a {
  color: #000;
  text-decoration: none;
}


