
#navbar {
  height: 3.5em;
  width: 100%;
  position: absolute;
  z-index: 10;
  top: 15px;
  left: 0;
  background-color: #33542d;
}
@media screen and (max-width: 500px) {
#navbar {
    position: fixed;
    top: 0;
    left: 0;
}
}
#navbar.scrolled {
  position: fixed;
  top: 0;
}
#navbar.active {
  height: 100vh;
}
#navbar a {
  color: #fff;
  text-decoration: none;
  text-align: center;
  width: 100%;
}
#navbar ul {
  list-style: none;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  vertical-align: baseline;
  margin: 0 auto;
  padding: 0;
}
@media screen and (max-width: 500px) {
#navbar ul {
    height: 100vh;
    display: none;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    background-color: transparent;
}
#navbar ul.active {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
}
#navbar ul li {
  height: 3.5em;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
}
#navbar .icon {
  height: 2.5em;
  width: 2.5em;
  margin: 0.5em;
  -webkit-mask: url("../assets/sitcon-logo.svg") no-repeat center;
  mask: url("../assets/sitcon-logo.svg") no-repeat center;
  background-color: #fff;
}
#navbar .toggle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@media screen and (min-width: 500px) {
#navbar .toggle {
    display: none;
}
}
#navbar .toggle a {
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
#navbar .toggle .text {
  font-size: 1.5rem;
}
#navbar .toggle .icon {
  margin: 1em;
  width: 2em;
  height: 2em;
  -webkit-mask: url("../assets/bars.svg") no-repeat center;
  mask: url("../assets/bars.svg") no-repeat center;
  background-color: #fff;
}


