
#admission {
  position: relative;
}
#admission:before {
  content: '';
  display: block;
  background-image: url("../assets/2017SC2.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 15rem;
  height: 30rem;
  margin-top: 12rem;
  position: absolute;
  right: -14rem;
  z-index: 1;
}
#admission .admissionList {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 auto;
  padding: 0;
  max-width: 600px;
}
@media screen and (min-width: 600px) {
#admission .admissionList {
    max-width: 90%;
}
}
#admission .admissionList > li {
  width: 33%;
  text-align: center;
  list-style: none;
}
@media screen and (min-width: 700px) {
#admission .admissionList > li {
    width: 20%;
}
}
#admission .button {
  font-size: 1.6rem;
  font-weight: 500;
  background-color: #1a887b;
  color: #f8fcf6;
  margin: 1em;
  padding: 0.4em 0.8em;
  transition: all 0.3s;
  border: 1px solid transparent;
}
#admission .button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  background-color: rgba(26,136,123,0.5);
  color: #f8fcf6;
  border: 1px solid rgba(26,136,123,0.5);
  transition: all 0.3s;
}
#admission .redcar {
  text-align: center;
}
#admission .redcar:before {
  content: '';
  vertical-align: -20%;
  display: inline-block;
  background-image: url("../assets/red-car.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 1.6rem;
  margin-right: 0.5rem;
  position: absolute;
  margin-left: -3.5rem;
  margin-top: 0.2rem;
  z-index: 1;
}
#admission .bluecar {
  text-align: center;
}
#admission .bluecar:before {
  content: '';
  vertical-align: -20%;
  display: inline-block;
  background-image: url("../assets/blue-car.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 1.6rem;
  margin-right: 0.5rem;
  position: absolute;
  margin-left: -3.5rem;
  margin-top: 0.2rem;
  z-index: 1;
}
.fourtitle {
  position: relative;
  margin-bottom: 4rem;
}
.fourtitle:before {
  content: '';
  display: inline-block;
  background-image: url("../assets/fourTitle.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 20rem;
  max-width: 90vw;
  height: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-left: -2.5rem;
}


